<template>
  <!--多平台终端接入开始-->
  <div class="multi-platform wrapper">
    <div class="container clearfix">
      <img src="/static/images/index/phone1.png" alt="" class="pull-left multi-platform-phone slide-left"/>
      <div class="pull-right multi-platform-des">
        <h1>{{ $t('translateLang.index.text14') }}</h1>
        <h5>{{ $t('translateLang.index.text15') }}</h5>
        <div class="code clearfix">
          <qrcode-vue :value="qrcode_Url" size="110" level="H" class="pull-left code-img"></qrcode-vue>
          <div class="pull-left">
            <a :href="androd_link" class="android"><img src="/static/images/index/android.png" alt="">Android</a>
            <a :href="ios_link" class="iphone"><img src="/static/images/index/iphone.png" alt="">Iphone</a>
          </div>
        </div>

      </div>
    </div>
  </div>
  <!--多平台终端接入结束-->
</template>

<script>
  import QrcodeVue from 'qrcode.vue'
  import { getAppDownLoad } from '@/api/index'
  export default {
    components: {
      QrcodeVue
    },
    data() {
      return {
        ios_link: '',
        androd_link: ''
      }
    },
    computed: {
      qrcode_Url() {
        if (this.$i18n.locale === 'zn') {
          return 'https://crypto.hkcexp.com/trade/appH5CN/h5App/downloadApp.html'
        }
        if (this.$i18n.locale === 'zh-hk') {
          return 'https://crypto.hkcexp.com/trade/appH5CN-tw/h5App/downloadApp.html'
        }
        if (this.$i18n.locale === 'en') {
          return 'https://crypto.hkcexp.com/trade/appH5EN/h5App/downloadApp.html'
        }
      }
    },
    mounted() {
      this.initAppLink()
    },
    methods: {
      initAppLink() {
        getAppDownLoad().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.ios_link = res.data.ios_link
          this.androd_link = res.data.androd_link
        }).catch(() => {
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .multi-platform{
    width: 100%;
    background: url(/static/images/index/bannerbj2.png) no-repeat;
    background-size: 100% 100%;
    color: white;
    height: 607px;
    min-width: 1200px;
    margin-bottom: -85px;
    h1{
      font-size: 40px;
      font-weight: normal;
    }
    h5{
      font-size: 18px;
      font-weight: normal;
      margin: 25px 0;
    }
    p{
      font-size: 14px;
      margin-bottom: 10px;
      padding-left: 17px;
      position: relative;
      i{
        width: 8px;
        height: 8px;
        background: #fff;
        border-radius: 50%;
        display: inline-block;
        margin-right: 10px;
        position: absolute;
        left: 0;
        top: 6px;
      }
    }
    .multi-platform-phone{
      margin-top: 75px;
    }
    .multi-platform-des{
      margin-top: 190px;
      width: 680px;
      text-align: left;
      .code{
        margin-top: 40px;
        // margin-left: 20px;
        >img{
          width: 110px;
          height: 110px;
        }
        .code-img{
          border: 4px solid white; background: white; width: 110px; height: 110px;
        }
        a{
          border-radius: 20px;
          display: block;
          margin-left: 20px;
          width: 160px;
          line-height: 42px;
          img{
            vertical-align: middle;
            margin: 0 15px 0 25px;
          }
          &.android{
            color: white;
            background: #01df9e;
            margin-bottom: 28px;
          }
          &.iphone{
            color: #31415d;
            background: #fff;
          }
        }
      }
    }
  }
</style>



